* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country__list {
  margin-bottom: 20px;
  font-size: 20px;
}

.country__list > .country__name {
  display: block;
  margin-left: 65px;
  margin-top: -38px;
}

.country__info > .country__name {
  font-weight: 700;
  font-size: 30px;
  margin-left: 15px;
}